@use 'base/variables' as v;
@use 'base/mixins' as m;

.confirmar {
    margin-top: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;

    .invitemanager {
        @include m.textoDegradado(v.$verde);
    }

    .acciones {
        justify-content: center;
        a {
            color: v.$verde;
        }
    }
}
